import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import {BarChart} from 'react-d3-components';
import Slider from "react-slick";
import * as d3 from "d3";
import {HorizontalBar, Line } from 'react-chartjs-2';
//var ReactCSSTransitionGroup = require('react-addons-css-transition-group');
const data = [{
    values: [{x: 'for overall offline exports', y: -1}, {x: 'for offline PayPal mirror basket', y: -5}, {x: 'for SMEs using PayPal', y: 20}]
  }];
  const data_1 = [
    {
    label: 'somethingA',
    values: [{x: '2013-2014', y: 6.2}, {x: '2014-2015', y: 14.5}, {x: '2015-2016', y: 33.2}, {x: '2016-2017', y: 36.9}]
    },
    {
    label: 'somethingB',
    values: [{x: '2013-2014', y: -9.9}, {x: '2014-2015', y: 17}, {x: '2015-2016', y: 1.7}, {x: '2016-2017', y: 1.1}]
    },
    {
    label: 'somethingC',
    values: [{x: '2013-2014', y: -9.1}, {x: '2014-2015', y: -7.4}, {x: '2015-2016', y: -5.6}, {x: '2016-2017', y: 3.5}]
    },
    {
    label: 'somethingD',
    values: [{x: '2013-2014', y: 1}, {x: '2014-2015', y: 0}, {x: '2015-2016', y: -0.5}, {x: '2016-2017', y: 1.7}]
    },
    {
    label: 'somethingE',
    values: [{x: '2013-2014', y: 2.9}, {x: '2014-2015', y: 2.9}, {x: '2015-2016', y: 2.5}, {x: '2016-2017', y: 3.2}]
    }
  ];
  const data_2= {
  labels: ['new entrants using PayPal', 'incumbent small businesses using PayPal'],
  datasets: [
    {
      backgroundColor: 'rgb(68, 114, 196)',
      borderColor: 'rgb(68, 114, 196)',
      borderWidth: 1,
      hoverBackgroundColor: 'rgb(68, 114, 196)',
      hoverBorderColor: 'rgb(68, 114, 196)',
      data: [27, -127]
    }
  ]
 
    
};
const options= {
    scales: {
        yAxes: [{ticks: {padding: -78,
        },
        scaleLabel:false,
      gridLines:false}]
    }
}
  const data_3 =  {
       labels: ['2013', '2014', '2015', '2016', '2017'],
       
    datasets: [
      {
      label: '',
      data: [100, 108, 110, 113, 120],
      lineTension: 0,
      fill: true,
      borderColor: 'rgb(31, 119, 180)',
      borderWidth:5,
      backgroundColor: 'transparent',
      pointBorderColor: 'rgb(31, 119, 180)',
      pointBackgroundColor: 'rgb(31, 119, 180)',
      pointRadius: 10,
      pointHoverRadius: 15,
      pointHitRadius: 30,
      pointBorderWidth: 2,
      pointStyle: 'circle',
      
      }
     
    ]
  }
  const options_1 = {
      legend: {
        display: false
    },
  scaleShowGridLines: true,
  scaleGridLineColor: 'rgba(0,0,0,.05)',
  scaleGridLineWidth: 1,
  scaleShowHorizontalLines: true,
  scaleShowVerticalLines: true,
  bezierCurve: true,
  bezierCurveTension: 0.4,
  pointDot: true,
  scales: {
    yAxes: [{
        ticks: {
            min: 98,
            callback: function(value, index, values) {
                return '';
            },
        },
        gridLines: {
            display: false,
            drawBorder: false,
            borderDash:[]
        },
    }]
  },
  pointDotRadius: 4,
  pointDotStrokeWidth: 1,
 
  datasetStroke: true,
  datasetStrokeWidth: 2,
  datasetFill: true,
  
}
 const sngl_bar = value => {
   return 'rgb(68, 114, 196)';
 };
class Argentina extends React.Component {
    constructor(props) {
        super(props);
        this.play = this.play.bind(this);
        this.pause = this.pause.bind(this);
        this.state = { width: window.innerWidth, }; // responsive mobile
        
      }
     
      play() {
        this.slider.slickPlay();
      }
      pause() {
        this.slider.slickPause();
      }
      // responsive mobile Start
        componentWillMount() {
          window.addEventListener('resize', this.handleWindowSizeChange);
        }

        // make sure to remove the listener
        // when the component is not mounted anymore
        componentWillUnmount() {
          window.removeEventListener('resize', this.handleWindowSizeChange);
        }

        handleWindowSizeChange = () => {
          this.setState({ width: window.innerWidth });
        };  

    // responsive mobile end... 
    render(){
        const settings = {
            dots: true,
            infinite: true,
            slidesToShow: 1,
            slidesToScroll: 1,
            autoplay: true,
            autoplaySpeed: 1000
          };
          const { width } = this.state; // responsive mobile
		      const isMobile = width <= 500; // responsive mobile
          if (isMobile) {
            return(
                <div>
               
                      <Slider ref={slider => (this.slider = slider)} {...settings}>
                       <div className="br_sldr_upr">
                          <div className="bar_graph bar_sldr">
                            {/* <h2 style={{textAlign: 'center'}}>Change in number of trade partners between 2013 and 2017</h2> */}
                            <BarChart
                              data={data}
                              width={320}
                              height={400}
                              colorScale={sngl_bar}
                              barPadding={0.8}
                              margin={{top: 10, bottom: 50, left: 50, right: 10}}
                              xAxis={{innerTickSize: 100}}
                              
                            />
                          </div>
                        </div>
                        <div className="br_sldr_upr">
                          <div className="bar_graph bar_sldr">
                            {/* <h2 style={{textAlign: 'center'}}>Change in number of trade partners between 2013 and 2017</h2> */}
                            <BarChart
                            groupedBars
                            data={data_1}
                            width={320}
                            height={400}
                            margin={{top: 10, bottom: 50, left: 50, right: 10}}
                            xAxis={{innerTickSize: 100}}
                            yAxis={{tickFormat:function(d) { return d3.format('d')(d*1) +'%'; } }}/>
                          </div>
                        </div>
                        <div className="br_sldr_upr">
                          <div className="bar_graph bar_sldr">
                            {/* <h2 style={{textAlign: 'center'}}>Change in number of trade partners between 2013 and 2017</h2> */}
                            <HorizontalBar data={data_2} options={options} />
                          </div>
                        </div>
                        <div className="br_sldr_upr">
                          <div className="bar_graph bar_sldr"> 
                            <Line data={data_3}
                            margin={{top: 10, bottom: 50, left: 50, right: 10}}
                            options={options_1}
                           width={320}
                            height={400}/>
                          </div>
                        </div>
                      </Slider>
                      
                      <div className="ply_pause_btn" style={{ textAlign: "center" }}>
                        <div className="both_ply_pause">
                          <button className="button" onClick={this.play}>
                            <img src="image/play_button.png" />
                          </button>
                          <button className="button pause_btn" onClick={this.pause}>
                            <img src="image/pause_button.png" />
                          </button>
                        </div>
                      </div>
                    </div>
              
              
                          
            );
          }
          else{
            return(
                <div>
               
                      <Slider ref={slider => (this.slider = slider)} {...settings}>
                       <div className="br_sldr_upr">
                          <div className="bar_graph bar_sldr">
                            {/* <h2 style={{textAlign: 'center'}}>Change in number of trade partners between 2013 and 2017</h2> */}
                            <BarChart
                              data={data}
                              width={680}
                              height={400}
                              colorScale={sngl_bar}
                              barPadding={0.8}
                              margin={{top: 10, bottom: 50, left: 50, right: 10}}
                              xAxis={{innerTickSize: 100}}
                              
                            />
                          </div>
                        </div>
                        <div className="br_sldr_upr">
                          <div className="bar_graph bar_sldr">
                            {/* <h2 style={{textAlign: 'center'}}>Change in number of trade partners between 2013 and 2017</h2> */}
                            <BarChart
                            groupedBars
                            data={data_1}
                            width={680}
                            height={400}
                            margin={{top: 10, bottom: 50, left: 50, right: 10}}
                            xAxis={{innerTickSize: 100}}
                            yAxis={{tickFormat:function(d) { return d3.format('d')(d*1) +'%'; } }}/>
                          </div>
                        </div>
                        <div className="br_sldr_upr">
                          <div className="bar_graph bar_sldr">
                            {/* <h2 style={{textAlign: 'center'}}>Change in number of trade partners between 2013 and 2017</h2> */}
                            <HorizontalBar data={data_2} options={options} />
                          </div>
                        </div>
                        <div className="br_sldr_upr">
                          <div className="bar_graph bar_sldr"> 
                            <Line data={data_3}
                            margin={{top: 10, bottom: 50, left: 50, right: 10}}
                            options={options_1}
                           width={680}
                            height={400}/>
                          </div>
                        </div>
                      </Slider>
                      
                      <div className="ply_pause_btn" style={{ textAlign: "center" }}>
                        <div className="both_ply_pause">
                          <button className="button" onClick={this.play}>
                            <img src="image/play_button.png" />
                          </button>
                          <button className="button pause_btn" onClick={this.pause}>
                            <img src="image/pause_button.png" />
                          </button>
                        </div>
                      </div>
                    </div>
              
              
                          
            );
          }
    }
} 
export default Argentina;
